.buttons__button {
  padding: 5px;
  border: none;
  color: teal;
  background: white;
  transition: 1s;
  cursor: pointer;
  margin-right: 20px;
}

.buttons__button:hover {
  color: rgb(2, 175, 175);
}

.js-hideAllDone {
  display: none;
}

.buttons__button:disabled {
  color: rgba(204, 204, 204, 0.953);
  cursor: not-allowed;
}

.buttons {
  display: flex;
}

@media (max-width: 768px) {
  .buttons {
    flex-direction: column;
  }
}
